body {
  background-color: #111;
  margin: 0;
  font-family: sans-serif;
}

.app-container {
  display: flex;
}

.main-content {
  flex: 1;
  /* padding: 2rem; */
  margin: 0 60px;
}
.welcome-overlay {
  position: absolute;
  inset: 0;
  background-color: #121212;
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  padding: 2rem;
  animation: fadeIn 1s ease-in-out;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: bold;
}

.fade-in {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 1s ease-out forwards;
}

.fade-in.delay-3 {
  animation-delay: 2.5s;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


